.stepOneCard {
  position: absolute;
  border-radius: 10px;
  background-color: white;
  padding: 2rem 1.5rem;
  text-align: left;
  width: 100%;
}
.stepOneCard section h1 {
  color: hsl(213deg, 96%, 18%);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.stepOneCard section p {
  line-height: 1.4rem;
  color: hsl(231deg, 11%, 63%);
}
.stepOneCard form {
  margin-top: 1.4rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.stepOneCard form div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.stepOneCard form div label {
  color: hsl(213deg, 96%, 18%);
  font-size: 0.8rem;
}
.stepOneCard form div input {
  padding: 0.6rem 1rem;
  border: 1px hsl(231deg, 11%, 63%) solid;
  border-radius: 5px;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
}
.stepOneCard form div input:focus {
  border: 1px transparent solid;
  outline: 1px hsl(213deg, 96%, 18%) solid;
}
.stepOneCard form .nextBtn {
  z-index: 1;
  position: absolute;
  bottom: -82%;
  right: 0%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  background-color: hsl(213deg, 96%, 18%);
  padding: 0.6rem 1rem;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

@media only screen and (min-width: 800px) {
  .stepOneCard {
    position: relative;
  }
  .stepOneCard section h1 {
    font-size: 2rem;
  }
  .stepOneCard section p {
    font-size: 1.2rem;
    font-weight: 100;
  }
  .stepOneCard form {
    margin-top: 1.8rem;
    gap: 1.5rem;
  }
  .stepOneCard form div {
    gap: 0.5rem;
  }
  .stepOneCard form div label {
    font-size: 1rem;
    font-weight: 500;
  }
  .stepOneCard form div input {
    padding: 0.8rem;
  }
  .stepOneCard form .nextBtn {
    bottom: -20%;
    font-size: 1rem;
  }
}/*# sourceMappingURL=stepOne.css.map */