.stepThreeCard {
  position: absolute;
  border-radius: 10px;
  background-color: white;
  padding: 2rem 1.5rem;
  text-align: left;
  width: 100%;
}
.stepThreeCard section h1 {
  color: hsl(213deg, 96%, 18%);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.stepThreeCard section p {
  line-height: 1.4rem;
  color: hsl(231deg, 11%, 63%);
}
.stepThreeCard form {
  position: relative;
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.stepThreeCard form .addOns {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  width: 100%;
  border: 1px hsl(231deg, 11%, 63%) solid;
  padding: 1rem;
  border-radius: 10px;
  transition: border 200ms ease-in-out;
}
.stepThreeCard form .addOns .details {
  margin-left: 0.2rem;
}
.stepThreeCard form .addOns .details .name {
  font-size: 1rem;
  margin-bottom: 0.2rem;
}
.stepThreeCard form .addOns .details .smallDis {
  font-size: 0.8rem;
}
.stepThreeCard form .addOns aside {
  color: hsl(243deg, 100%, 62%);
  margin-left: auto;
  font-size: 0.8rem;
}
.stepThreeCard form .addOns .checkbox {
  width: 1rem;
  border-radius: 5px;
  height: 1rem;
  border: 1px hsl(231deg, 11%, 63%) solid;
  transition: all 200ms ease-in-out;
  position: relative;
}
.stepThreeCard form .addOns .checkbox img {
  position: absolute;
  width: 80%;
  inset: 0;
  margin: auto;
  visibility: hidden;
  transition: all 300ms ease-in;
}
.stepThreeCard form .addOns .selected {
  border: none;
  background-color: hsl(243deg, 100%, 62%);
}
.stepThreeCard form .addOns .selected img {
  visibility: visible;
}
.stepThreeCard form .addOns input {
  display: none;
}
.stepThreeCard form .selectedOpt {
  border: 1px hsl(243deg, 100%, 62%) solid;
}
.stepThreeCard form .nextBtn,
.stepThreeCard form .goBack {
  font-weight: 400;
  z-index: 1;
  position: fixed;
  bottom: 1%;
  right: 4%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  background-color: hsl(213deg, 96%, 18%);
  padding: 0.6rem 1rem;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}
.stepThreeCard form .goBack {
  background-color: transparent;
  color: hsl(231deg, 11%, 63%);
  left: 3%;
}

@media only screen and (min-width: 800px) {
  .stepThreeCard {
    margin-top: -15%;
    position: relative;
  }
  .stepThreeCard section h1 {
    font-size: 2rem;
  }
  .stepThreeCard section p {
    font-size: 1.2rem;
    font-weight: 100;
  }
  .stepThreeCard form {
    margin-top: 3rem;
    gap: 2rem;
  }
  .stepThreeCard form .radio {
    flex-direction: row;
  }
  .stepThreeCard form .radio .addOns {
    flex-direction: column;
    width: 100%;
    gap: 2.5rem;
  }
  .stepThreeCard form .radio .addOns img {
    width: 3rem;
  }
  .stepThreeCard form .radio .addOns .data {
    gap: 0.8rem;
  }
  .stepThreeCard form .radio .addOns .data h3 {
    font-size: 1.3rem;
  }
  .stepThreeCard form .radio .addOns .data span {
    font-size: 1rem;
  }
  .stepThreeCard form .radio .addOns .data p {
    font-size: 0.9rem;
  }
  .stepThreeCard form .nextBtn,
.stepThreeCard form .goBack {
    bottom: 2%;
    font-size: 1rem;
    right: 14%;
  }
  .stepThreeCard form .goBack {
    left: 40%;
  }
  .stepThreeCard form .goBack:hover {
    color: hsl(213deg, 96%, 18%);
  }
}/*# sourceMappingURL=stepThree.css.map */