.stepOneCard {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    padding: 2rem 1.5rem;
    text-align: left;
    width: 100%;


    section {
        h1 {
            color: hsl(213, 96%, 18%);
            font-size: 1.3rem;
            margin-bottom: 1rem;
        }

        p {
            line-height: 1.4rem;
            color: hsl(231, 11%, 63%);
        }
    }

    form {
        margin-top: 1.4rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;

        div {
            display: flex;
            flex-direction: column;
            gap: .5rem;

            label {
                color: hsl(213, 96%, 18%);
                font-size: .8rem;
            }

            input {
                padding: .6rem 1rem;
                border: 1px hsl(231, 11%, 63%) solid;
                border-radius: 5px;
                appearance: textfield;

                &:focus {
                    border: 1px transparent solid;
                    outline: 1px hsl(213, 96%, 18%) solid;
                }
            }

        }

        .nextBtn {
            z-index: 1;
            position: absolute;
            bottom: -82%;
            right: 0%;
            width: fit-content;
            border: none;
            background-color: hsl(213, 96%, 18%);
            padding: .6rem 1rem;
            border-radius: 5px;
            color: white;
            cursor: pointer;
        }

    }

}

@media only screen and (min-width: 800px) {
    .stepOneCard {
        position: relative;

        section {
            h1 {
                font-size: 2rem;
            }

            p {
                font-size: 1.2rem;
                font-weight: 100;
            }
        }

        form {
            margin-top: 1.8rem;
            gap: 1.5rem;

            div {
                gap: .5rem;

                label {
                    font-size: 1rem;
                    font-weight: 500;
                }

                input {
                    padding: .8rem;
                }
            }

            .nextBtn {
                bottom: -20%;
                font-size: 1rem;


            }
        }
    }

}