.stepTwoCard {
  position: absolute;
  border-radius: 10px;
  background-color: white;
  padding: 2rem 1.5rem;
  text-align: left;
  width: 100%;
}
.stepTwoCard section h1 {
  color: hsl(213deg, 96%, 18%);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.stepTwoCard section p {
  line-height: 1.4rem;
  color: hsl(231deg, 11%, 63%);
}
.stepTwoCard form {
  position: relative;
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.stepTwoCard form .radio {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.stepTwoCard form .options {
  border: 1px hsl(231deg, 11%, 63%) solid;
  border-radius: 10px;
  padding: 1rem;
  display: flex;
  gap: 1.2rem;
  align-items: flex-start;
  cursor: pointer;
  transition: all 100ms ease-in-out;
}
.stepTwoCard form .options .data {
  display: grid;
  gap: 0.4rem;
}
.stepTwoCard form .options .data h3 {
  font-size: 1rem;
  color: hsl(213deg, 96%, 18%);
}
.stepTwoCard form .options .data span {
  font-size: 0.9rem;
  color: hsl(231deg, 11%, 63%);
}
.stepTwoCard form .options .data p {
  height: 0;
  opacity: 0;
  font-size: 0.8rem;
  color: hsl(213deg, 96%, 18%);
  transition: all 300ms ease-in-out;
}
.stepTwoCard form .options .data .activeBonus {
  height: 1rem;
  opacity: 1;
  display: block;
}
.stepTwoCard form .options input {
  display: none;
}
.stepTwoCard form .options:hover {
  border: 1px hsl(243deg, 100%, 62%) solid;
  background-color: hsla(243deg, 100%, 62%, 0.1);
}
.stepTwoCard form .active {
  border: 1px hsl(243deg, 100%, 62%) solid;
  background-color: hsla(243deg, 100%, 62%, 0.1);
}
.stepTwoCard form .planPay {
  margin: 1.5rem auto;
  display: flex;
  gap: 1.2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
}
.stepTwoCard form .planPay p {
  font-weight: 500;
  font-size: 1rem;
  color: hsl(231deg, 11%, 63%);
  transition: all 200ms ease-in-out;
}
.stepTwoCard form .planPay .activeType {
  color: hsl(213deg, 96%, 18%);
}
.stepTwoCard form .planPay .switch {
  width: 2.5rem;
  border-radius: 100px;
  height: 1.2rem;
  background-color: hsl(213deg, 96%, 18%);
  display: flex;
  align-items: center;
  padding: 0.2rem;
}
.stepTwoCard form .planPay .switch .switchCursor {
  width: 0.85rem;
  height: 0.8rem;
  background-color: white;
  border-radius: 100%;
  transition: all 200ms ease-in-out;
}
.stepTwoCard form .planPay .switch .switchActive {
  transform: translateX(140%);
}
.stepTwoCard form .planPay input {
  display: none;
}
.stepTwoCard form .nextBtn,
.stepTwoCard form .goBack {
  font-weight: 400;
  z-index: 1;
  position: fixed;
  bottom: 1%;
  right: 4%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  background-color: hsl(213deg, 96%, 18%);
  padding: 0.6rem 1rem;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}
.stepTwoCard form .goBack {
  background-color: transparent;
  color: hsl(231deg, 11%, 63%);
  left: 3%;
}

@media only screen and (min-width: 800px) {
  .stepTwoCard {
    margin-top: -15%;
    position: relative;
  }
  .stepTwoCard section h1 {
    font-size: 2rem;
  }
  .stepTwoCard section p {
    font-size: 1.2rem;
    font-weight: 100;
  }
  .stepTwoCard form {
    margin-top: 3rem;
    gap: 2rem;
  }
  .stepTwoCard form .radio {
    flex-direction: row;
  }
  .stepTwoCard form .radio .options {
    flex-direction: column;
    width: 100%;
    gap: 2.5rem;
  }
  .stepTwoCard form .radio .options img {
    width: 3rem;
  }
  .stepTwoCard form .radio .options .data {
    gap: 0.8rem;
  }
  .stepTwoCard form .radio .options .data h3 {
    font-size: 1.3rem;
  }
  .stepTwoCard form .radio .options .data span {
    font-size: 1rem;
  }
  .stepTwoCard form .radio .options .data p {
    font-size: 0.9rem;
  }
  .stepTwoCard form .planPay {
    cursor: pointer;
  }
  .stepTwoCard form .planPay p {
    font-size: 1.2rem;
  }
  .stepTwoCard form .nextBtn,
.stepTwoCard form .goBack {
    bottom: 2%;
    font-size: 1rem;
    right: 14%;
  }
  .stepTwoCard form .goBack {
    left: 40%;
  }
  .stepTwoCard form .goBack:hover {
    color: hsl(213deg, 96%, 18%);
  }
}/*# sourceMappingURL=stepTwo.css.map */