.stepThreeCard {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    padding: 2rem 1.5rem;
    text-align: left;
    width: 100%;


    section {
        h1 {
            color: hsl(213, 96%, 18%);
            font-size: 1.3rem;
            margin-bottom: 1rem;
        }

        p {
            line-height: 1.4rem;
            color: hsl(231, 11%, 63%);
        }
    }

    form {
        position: relative;
        margin-top: 1.5rem;
        display: flex;
        flex-direction: column;
        gap: 1.2rem;

        .addOns {
            display: flex;
            align-items: center;
            gap: .8rem;
            width: 100%;
            border: 1px hsl(231, 11%, 63%) solid;
            padding: 1rem;
            border-radius: 10px;
            transition: border 200ms ease-in-out;

            .details {
                margin-left: .2rem;

                .name {
                    font-size: 1rem;
                    margin-bottom: .2rem;
                }

                .smallDis {
                    font-size: .8rem;
                }
            }

            aside {
                color: hsl(243, 100%, 62%);
                margin-left: auto;
                font-size: .8rem;
            }

            .checkbox {
                width: 1rem;
                border-radius: 5px;
                height: 1rem;
                border: 1px hsl(231, 11%, 63%) solid;
                transition: all 200ms ease-in-out;
                position: relative;

                img {
                    position: absolute;
                    width: 80%;
                    inset: 0;
                    margin: auto;
                    visibility: hidden;
                    transition: all 300ms ease-in;
                }
            }

            .selected {
                border: none;
                background-color: hsl(243, 100%, 62%);

                img {
                    visibility: visible;
                }
            }



            input {
                display: none;
            }
        }

        .selectedOpt {
            border: 1px hsl(243, 100%, 62%) solid;
        }

        .nextBtn,
        .goBack {
            font-weight: 400;
            z-index: 1;
            position: fixed;
            bottom: 1%;
            right: 4%;
            width: fit-content;
            border: none;
            background-color: hsl(213, 96%, 18%);
            padding: .6rem 1rem;
            border-radius: 5px;
            color: white;
            cursor: pointer;
        }

        .goBack {
            background-color: transparent;
            color: hsl(231, 11%, 63%);
            left: 3%;
        }

    }


}




@media only screen and (min-width: 800px) {
    .stepThreeCard {
        margin-top: -15%;
        position: relative;

        section {
            h1 {
                font-size: 2rem;
            }

            p {
                font-size: 1.2rem;
                font-weight: 100;
            }
        }

        form {
            margin-top: 3rem;
            gap: 2rem;

            .radio {
                flex-direction: row;

                .addOns {
                    flex-direction: column;
                    width: 100%;
                    gap: 2.5rem;

                    img {
                        width: 3rem;
                    }

                    .data {
                        gap: .8rem;

                        h3 {
                            font-size: 1.3rem;
                        }

                        span {
                            font-size: 1rem;
                        }

                        p {
                            font-size: .9rem;
                        }
                    }
                }
            }



            .nextBtn,
            .goBack {
                bottom: 2%;
                font-size: 1rem;
                right: 14%;
            }

            .goBack {
                left: 40%;

                &:hover {
                    color: hsl(213, 96%, 18%);
                }
            }
        }
    }

}