.stepFourCard {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    padding: 2rem 1.5rem;
    text-align: left;
    width: 100%;


    section {
        h1 {
            color: hsl(213, 96%, 18%);
            font-size: 1.3rem;
            margin-bottom: 1rem;
        }

        p {
            line-height: 1.4rem;
            color: hsl(231, 11%, 63%);
        }
    }

    .final {
        display: flex;
        flex-direction: column;
        margin: 2rem auto 0;
        width: 95%;
        gap: 3rem;

        .items {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            background-color: hsl(217, 100%, 97%);
            padding: .8rem;

            hr {
                height: 1px;
                margin-inline: auto;
                width: 95%;
                border: none;
                background-color: hsl(231, 11%, 63%);
            }

            .plan {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .details {
                    h3 {
                        font-size: .9rem;
                        color: hsl(213, 96%, 18%);
                    }

                    button {
                        border: none;
                        color: hsl(231, 11%, 63%);
                        background-color: transparent;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }

                .price {
                    font-size: .8rem;
                    font-weight: 600;
                    color: hsl(213, 96%, 18%);
                }
            }

            .addOns {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                .addOn {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .name {
                        font-size: .8rem;
                        color: hsl(231, 11%, 63%);
                    }

                    .price {
                        font-size: .8rem;
                        color: hsl(213, 96%, 18%);
                    }
                }
            }

        }

        .total {
            padding: .8rem;
            display: flex;
            justify-content: space-between;

            p {
                font-size: .8rem;
                color: hsl(231, 11%, 63%);
            }

            .totalPrice {
                color: hsl(243, 100%, 62%);
                font-size: 1rem;
                font-weight: 600;
            }
        }
    }

    .nextBtn,
    .goBack {
        font-weight: 400;
        z-index: 1;
        position: fixed;
        bottom: 1%;
        right: 4%;
        width: fit-content;
        border: none;
        background-color: hsl(213, 96%, 18%);
        padding: .6rem 1rem;
        border-radius: 5px;
        color: white;
        cursor: pointer;
    }

    .goBack {
        background-color: transparent;
        color: hsl(231, 11%, 63%);
        left: 3%;
    }
}




@media only screen and (min-width: 800px) {
    .stepFourCard {
        margin-top: -15%;
        position: relative;

        section {
            h1 {
                font-size: 2rem;
            }

            p {
                font-size: 1.2rem;
                font-weight: 100;
            }
        }

        .final {
            margin-top: 3rem;
            gap: 2rem;

            .items {
                gap: 1.5rem;

                .plan {
                    .details {


                        h3 {
                            font-size: 1.2rem;
                        }

                        button {
                            margin-top: .5rem;
                            font-size: 1rem;
                        }
                    }

                    .price {
                        font-size: 1.2rem;
                    }
                }

                .addOns {
                    gap: 1.2rem;

                    .addOn {
                        .name {
                            font-size: 1rem;
                        }

                        .price {
                            font-size: 1rem;
                        }
                    }

                }
            }

            .total {
                p {
                    font-size: 1rem;
                }

                .totalPrice {
                    font-size: 1.2rem;
                }
            }




        }

        .nextBtn,
        .goBack {
            bottom: 2%;
            font-size: 1rem;
            right: 14%;
        }

        .goBack {
            left: 40%;

            &:hover {
                color: hsl(213, 96%, 18%);
            }
        }
    }

}