.navbar {
  background: url("../assets/images/bg-sidebar-mobile.svg") no-repeat fixed top;
  height: 12rem;
  padding-top: 2rem;
  background-size: contain;
}
.navbar .steps {
  display: flex;
  width: 55%;
  margin-inline: auto;
  justify-content: space-between;
}
.navbar .steps .step {
  pointer-events: none;
}
.navbar .steps .step .no {
  padding: 0.4rem 0.7rem;
  border: 1px white solid;
  color: white;
  border-radius: 100%;
  font-size: 1.2rem;
}
.navbar .steps .step .currentStep {
  border: 1px transparent solid !important;
  background-color: hsl(228deg, 100%, 84%);
  color: hsl(213deg, 96%, 18%) !important;
  font-weight: 600;
}
.navbar .steps .step .stepDetail {
  display: none;
}

@media only screen and (min-width: 800px) {
  .navbar {
    border-radius: 0;
    height: 100%;
    border-radius: 10px;
    width: 25%;
    background: url("../assets/images/bg-sidebar-desktop.svg") no-repeat top;
    background-size: cover;
  }
  .navbar .steps {
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    gap: 2rem;
    margin-left: 2rem;
  }
  .navbar .steps .step {
    padding: 0.5rem 0.8rem;
    position: relative;
    display: flex;
    gap: 1rem;
  }
  .navbar .steps .step .stepDetail {
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: space-between;
  }
  .navbar .steps .step .stepDetail .stepNo {
    font-size: 0.8rem;
    font-weight: 200;
    color: hsl(228deg, 100%, 84%);
  }
  .navbar .steps .step .stepDetail .stepName {
    font-size: 1rem;
    font-weight: 500;
    color: white;
  }
}/*# sourceMappingURL=navbar.css.map */