.navbar {
    background: url("../assets/images/bg-sidebar-mobile.svg") no-repeat fixed top;
    height: min(12rem, 12rem);
    padding-top: 2rem;
    background-size: contain;

    .steps {
        display: flex;
        width: 55%;
        margin-inline: auto;
        justify-content: space-between;



        .step {

            pointer-events: none;

            .no {
                padding: .4rem .7rem;
                border: 1px white solid;
                color: white;
                border-radius: 100%;
                font-size: 1.2rem;
            }

            .currentStep {
                border: 1px transparent solid !important;
                background-color: hsl(228, 100%, 84%);
                color: hsl(213, 96%, 18%) !important;
                font-weight: 600;
            }

            .stepDetail {
                display: none;
            }
        }

    }


}


@media only screen and (min-width: 800px) {
    .navbar {
        border-radius: 0;
        height: 100%;
        border-radius: 10px;
        width: 25%;
        background: url('../assets/images/bg-sidebar-desktop.svg') no-repeat top;
        background-size: cover;

        .steps {
            flex-direction: column;
            width: fit-content;
            gap: 2rem;
            margin-left: 2rem;

            .step {
                padding: .5rem .8rem;
                position: relative;
                display: flex;
                gap: 1rem;


                .stepDetail {
                    display: flex;
                    flex-direction: column;
                    text-align: left;
                    justify-content: space-between;

                    .stepNo {
                        font-size: .8rem;
                        font-weight: 200;
                        color: hsl(228, 100%, 84%);
                    }

                    .stepName {
                        font-size: 1rem;
                        font-weight: 500;
                        color: white;
                    }
                }
            }
        }
    }
}