.stepFourCard {
  position: absolute;
  border-radius: 10px;
  background-color: white;
  padding: 2rem 1.5rem;
  text-align: left;
  width: 100%;
}
.stepFourCard section h1 {
  color: hsl(213deg, 96%, 18%);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.stepFourCard section p {
  line-height: 1.4rem;
  color: hsl(231deg, 11%, 63%);
}
.stepFourCard .final {
  display: flex;
  flex-direction: column;
  margin: 2rem auto 0;
  width: 95%;
  gap: 3rem;
}
.stepFourCard .final .items {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: hsl(217deg, 100%, 97%);
  padding: 0.8rem;
}
.stepFourCard .final .items hr {
  height: 1px;
  margin-inline: auto;
  width: 95%;
  border: none;
  background-color: hsl(231deg, 11%, 63%);
}
.stepFourCard .final .items .plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.stepFourCard .final .items .plan .details h3 {
  font-size: 0.9rem;
  color: hsl(213deg, 96%, 18%);
}
.stepFourCard .final .items .plan .details button {
  border: none;
  color: hsl(231deg, 11%, 63%);
  background-color: transparent;
  text-decoration: underline;
  cursor: pointer;
}
.stepFourCard .final .items .plan .price {
  font-size: 0.8rem;
  font-weight: 600;
  color: hsl(213deg, 96%, 18%);
}
.stepFourCard .final .items .addOns {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.stepFourCard .final .items .addOns .addOn {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.stepFourCard .final .items .addOns .addOn .name {
  font-size: 0.8rem;
  color: hsl(231deg, 11%, 63%);
}
.stepFourCard .final .items .addOns .addOn .price {
  font-size: 0.8rem;
  color: hsl(213deg, 96%, 18%);
}
.stepFourCard .final .total {
  padding: 0.8rem;
  display: flex;
  justify-content: space-between;
}
.stepFourCard .final .total p {
  font-size: 0.8rem;
  color: hsl(231deg, 11%, 63%);
}
.stepFourCard .final .total .totalPrice {
  color: hsl(243deg, 100%, 62%);
  font-size: 1rem;
  font-weight: 600;
}
.stepFourCard .nextBtn,
.stepFourCard .goBack {
  font-weight: 400;
  z-index: 1;
  position: fixed;
  bottom: 1%;
  right: 4%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  background-color: hsl(213deg, 96%, 18%);
  padding: 0.6rem 1rem;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}
.stepFourCard .goBack {
  background-color: transparent;
  color: hsl(231deg, 11%, 63%);
  left: 3%;
}

@media only screen and (min-width: 800px) {
  .stepFourCard {
    margin-top: -15%;
    position: relative;
  }
  .stepFourCard section h1 {
    font-size: 2rem;
  }
  .stepFourCard section p {
    font-size: 1.2rem;
    font-weight: 100;
  }
  .stepFourCard .final {
    margin-top: 3rem;
    gap: 2rem;
  }
  .stepFourCard .final .items {
    gap: 1.5rem;
  }
  .stepFourCard .final .items .plan .details h3 {
    font-size: 1.2rem;
  }
  .stepFourCard .final .items .plan .details button {
    margin-top: 0.5rem;
    font-size: 1rem;
  }
  .stepFourCard .final .items .plan .price {
    font-size: 1.2rem;
  }
  .stepFourCard .final .items .addOns {
    gap: 1.2rem;
  }
  .stepFourCard .final .items .addOns .addOn .name {
    font-size: 1rem;
  }
  .stepFourCard .final .items .addOns .addOn .price {
    font-size: 1rem;
  }
  .stepFourCard .final .total p {
    font-size: 1rem;
  }
  .stepFourCard .final .total .totalPrice {
    font-size: 1.2rem;
  }
  .stepFourCard .nextBtn,
.stepFourCard .goBack {
    bottom: 2%;
    font-size: 1rem;
    right: 14%;
  }
  .stepFourCard .goBack {
    left: 40%;
  }
  .stepFourCard .goBack:hover {
    color: hsl(213deg, 96%, 18%);
  }
}/*# sourceMappingURL=stepFour.css.map */