.mainFormContainer {
  position: relative;
  background-color: hsl(229deg, 24%, 87%);
  height: 100%;
}
.mainFormContainer .card {
  position: absolute;
  inset: 0;
  top: -3.8rem;
  width: 90%;
  margin-inline: auto;
}
.mainFormContainer .bottom {
  z-index: 0;
  position: absolute;
  height: 4rem;
  width: 100%;
  bottom: 0;
  right: 0;
  background-color: white;
}

@media only screen and (min-width: 800px) {
  .mainFormContainer {
    width: 80%;
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .mainFormContainer .card {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 60%;
    top: 0;
    position: relative;
  }
  .mainFormContainer .bottom {
    display: none;
  }
}/*# sourceMappingURL=mainForm.css.map */