.stepTwoCard {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    padding: 2rem 1.5rem;
    text-align: left;
    width: 100%;


    section {
        h1 {
            color: hsl(213, 96%, 18%);
            font-size: 1.3rem;
            margin-bottom: 1rem;
        }

        p {
            line-height: 1.4rem;
            color: hsl(231, 11%, 63%);
        }
    }

    form {
        position: relative;
        margin-top: 1.5rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .radio {
            display: flex;
            flex-direction: column;
            gap: 1.2rem;
        }

        .options {
            border: 1px hsl(231, 11%, 63%) solid;
            border-radius: 10px;
            padding: 1rem;
            display: flex;
            gap: 1.2rem;
            align-items: flex-start;
            cursor: pointer;
            transition: all 100ms ease-in-out;

            .data {
                display: grid;
                gap: .4rem;

                h3 {
                    font-size: 1rem;
                    color: hsl(213, 96%, 18%);
                }

                span {
                    font-size: .9rem;
                    color: hsl(231, 11%, 63%);
                }

                p {
                    height: 0;
                    opacity: 0;
                    font-size: .8rem;
                    color: hsl(213, 96%, 18%);
                    transition: all 300ms ease-in-out;
                }

                .activeBonus {
                    height: 1rem;
                    opacity: 1;
                    display: block;
                }


            }

            input {
                display: none;
            }


            &:hover {
                border: 1px hsl(243, 100%, 62%) solid;
                background-color: hsla(243, 100%, 62%, .1)
            }
        }

        .active {
            border: 1px hsl(243, 100%, 62%) solid;
            background-color: hsla(243, 100%, 62%, .1);
        }

        .planPay {
            margin: 1.5rem auto;
            display: flex;
            gap: 1.2rem;
            width: fit-content;
            align-items: center;

            p {
                font-weight: 500;
                font-size: 1rem;
                color: hsl(231, 11%, 63%);
                transition: all 200ms ease-in-out;
            }

            .activeType {
                color: hsl(213, 96%, 18%);
            }

            .switch {
                width: 2.5rem;
                border-radius: 100px;
                height: 1.2rem;
                background-color: hsl(213, 96%, 18%);
                display: flex;
                align-items: center;
                padding: .2rem;

                .switchCursor {

                    width: .85rem;
                    height: .8rem;
                    background-color: white;
                    border-radius: 100%;
                    transition: all 200ms ease-in-out;
                }


                .switchActive {
                    transform: translateX(140%);
                }
            }

            input {
                display: none;
            }
        }



        .nextBtn,
        .goBack {
            font-weight: 400;
            z-index: 1;
            position: fixed;
            bottom: 1%;
            right: 4%;
            width: fit-content;
            border: none;
            background-color: hsl(213, 96%, 18%);
            padding: .6rem 1rem;
            border-radius: 5px;
            color: white;
            cursor: pointer;
        }

        .goBack {
            background-color: transparent;
            color: hsl(231, 11%, 63%);
            left: 3%;
        }

    }
}


@media only screen and (min-width: 800px) {
    .stepTwoCard {
        margin-top: -15%;
        position: relative;

        section {
            h1 {
                font-size: 2rem;
            }

            p {
                font-size: 1.2rem;
                font-weight: 100;
            }
        }

        form {
            margin-top: 3rem;
            gap: 2rem;

            .radio {
                flex-direction: row;

                .options {
                    flex-direction: column;
                    width: 100%;
                    gap: 2.5rem;

                    img {
                        width: 3rem;
                    }

                    .data {
                        gap: .8rem;

                        h3 {
                            font-size: 1.3rem;
                        }

                        span {
                            font-size: 1rem;
                        }

                        p {
                            font-size: .9rem;
                        }
                    }
                }
            }

            .planPay {
                cursor: pointer;

                p {
                    font-size: 1.2rem;
                }
            }


            .nextBtn,
            .goBack {
                bottom: 2%;
                font-size: 1rem;
                right: 14%;
            }

            .goBack {
                left: 40%;

                &:hover {
                    color: hsl(213, 96%, 18%);
                }
            }
        }
    }

}