.thankYouCard {
    position: absolute;
    border-radius: 10px;
    background-color: white;
    padding: 4rem 1.5rem;
    text-align: left;
    width: 100%;


    section {
        img {
            width: 4rem;
            margin-bottom: 1rem;

        }

        text-align: center;

        h1 {
            color: hsl(213, 96%, 18%);
            font-size: 1.3rem;
            margin-bottom: 1rem;
        }

        p {
            font-size: 1rem;
            line-height: 1.5rem;
            color: hsl(231, 11%, 63%);
        }
    }
}

@media only screen and (min-width: 800px) {
    .thankYouCard {
        margin-top: -15%;
        position: relative;

        section {
            h1 {
                font-size: 2rem;
            }

            p {
                font-size: 1.2rem;
                font-weight: 100;
            }
        }
    }
}