.thankYouCard {
  position: absolute;
  border-radius: 10px;
  background-color: white;
  padding: 4rem 1.5rem;
  text-align: left;
  width: 100%;
}
.thankYouCard section {
  text-align: center;
}
.thankYouCard section img {
  width: 4rem;
  margin-bottom: 1rem;
}
.thankYouCard section h1 {
  color: hsl(213deg, 96%, 18%);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.thankYouCard section p {
  font-size: 1rem;
  line-height: 1.5rem;
  color: hsl(231deg, 11%, 63%);
}

@media only screen and (min-width: 800px) {
  .thankYouCard {
    margin-top: -15%;
    position: relative;
  }
  .thankYouCard section h1 {
    font-size: 2rem;
  }
  .thankYouCard section p {
    font-size: 1.2rem;
    font-weight: 100;
  }
}/*# sourceMappingURL=thankyou.css.map */